//容器
.container {
    // 公司信息
    .company-info {
        height: 225px;
        padding: 10px;
        // background-color: #ccc;
        border: 1px solid #e8e8e8;
        img {
            float: left;
            display: block;
            width: 300px;
            height: 225px;
            background: #fff url(../images/public/loading5.gif) no-repeat;
            background-size: 100%;
        }
        .m-info {
            float: left;
            width: 600px;
            height: 225px;
            background-color: #fff;
            margin-left: 10px;
            padding-left: 20px;
            border-left: 1px solid #e8e8e8;
            .company {
                margin-top: 10px;
                letter-spacing: 3px;
                font-size: 30px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            .sale {
                margin-top: 30px;
                font-size: 20px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            .add {
                margin-top: 20px;
                font-size: 20px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            .contact {
                margin-top: 50px;
                .btn {
                    display: inline-block;
                    font-size: 18px;
                    padding: 4px 20px;
                    margin-right: 10px;
                    background-color: #f08c51;
                    border: 2px solid #f08c51;
                    color: #fff;
                    border-radius: 6px;
                    cursor: pointer;
                    transition: all .3s;
                    &:hover {
                        border: 2px solid #666;
                        background-color: #fff;
                        color: #666;
                    }
                }
            }
        }
    }
    //tab切换
    .tab-list {
        margin-top: 30px;
        height: 44px;
        background: #f6f6f6;
        padding-right: 20px;
        border: 1px solid #E5E5E5;
        .tab-btn {
            float: left;
            width: 160px;
            height: 44px;
            line-height: 44px;
            font-size: 16px;
            text-align: center;
            border-right: 1px solid #E5E5E5;
            cursor: pointer;
        }
        .tab-active {
            border-top: 2px solid #f08c51;
            background-color: #fff;
            margin-top: -1px;
            color: #ed593f;
        }
    }
    .tab-cont {
        // height: 500px;
        padding-top: 50px;
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 50px;
        border-left: 1px solid #E5E5E5;
        border-right: 1px solid #E5E5E5;
        // 公司服务产品模块
        .product {
            // display: none;
            .piece-middle {
                width: 100%;
                // margin-top: 15px;
                // height: 2295px;
                padding-left: 50px;
                transition: all .2s;
                .case-info {
                    float: left;
                    display: block;
                    width: 280px;
                    // height: 450px;
                    min-height: 330px;
                    margin-right: 30px;
                    margin-bottom: 30px;
                    padding: 10px;
                    padding-bottom: 20px;
                    // padding: 10px;
                    background-color: #fff;
                    border: 1px solid #ccc;
                    transition: all .3s;
                    &:hover {
                        transform: translate(0, -10px);
                        box-shadow: 5px 5px 30px -2px #666;
                    }
                    .picbox {
                        width: 280px;
                        height: 250px;
                        // background: #f5f5f6 url(../images/loading5.gif) 0px 0px no-repeat;
                        background: url(../images/public/loading5.gif) no-repeat;
                        background-size: 100%;
                        img {
                            display: block;
                            width: 280px;
                            height: 250px;
                        }
                    }
                    .tit {
                        font-size: 22px;
                        font-weight: normal;
                        margin-top: 20px;
                        color: #333;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                    .info {
                        margin-top: 20px;
                        font-size: 16px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                    .address {
                        margin-top: 5px;
                        font-size: 16px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                    .conpany {
                        position: relative;
                        margin-top: 20px;
                        font-size: 18px;
                        height: 22px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        .btn {
                            position: absolute;
                            right: 0;
                            margin-right: 10px;
                            color: #fff;
                            background-color: #f08c51;
                            padding: 0 10px;
                            border-radius: 11px;
                            line-height: 22px;
                            font-size: 14px;
                        }
                    }
                    &:hover .btn {
                        animation: style2 .7s infinite;
                        animation-iteration-count: 1;
                    }
                    @keyframes style2 {
                        from {
                            transform: scale3d(1, 1, 1);
                        }
                        30% {
                            transform: scale3d(1.1, 0.75, 1);
                        }
                        40% {
                            transform: scale3d(0.75, 1.1, 1);
                        }
                        50% {
                            transform: scale3d(1.07, 0.85, 1);
                        }
                        65% {
                            transform: scale3d(.95, 1.02, 1);
                        }
                        75% {
                            transform: scale3d(1.02, .95, 1);
                        }
                        to {
                            transform: scale3d(1, 1, 1);
                        }
                    }
                }
            }
        }
        //公司简介
        .summary {
            // padding-top: 50px;
            padding-left: 50px;
            padding-right: 50px;
            // height: 500px;
            // background-color: #ccc;
            .tit {
                font-size: 22px;
                font-weight: normal;
            }
            .address {
                margin-top: 40px;
                font-size: 18px;
            }
            .article {
                margin-top: 40px;
                font-size: 18px;
                line-height: 30px;
                letter-spacing: 1px;
            }
            .show {
                font-size: 18px;
                margin-top: 40px;
                .pic-box {
                    padding-top: 20px;
                    img {
                        float: left;
                        margin-right: 10px;
                        display: block;
                        width: 300px;
                        height: 250px;
                        background-color: #f2f2f2;
                    }
                }
            }
            .certificate {
                font-size: 18px;
                margin-top: 40px;
                .pic-box {
                    padding-top: 20px;
                    img {
                        float: left;
                        margin-right: 10px;
                        display: block;
                        width: 300px;
                        height: 250px;
                        background-color: #f2f2f2;
                    }
                }
            }
        }
        //店铺评价
        .appraise {
            // padding-top: 50px;
            // height: 500px;
            background-color: pink;
        }
    }
}